.glow-on-hover {
    font-size: 19px;
    width: 100%;
    height: 70px;
    border: none;
    outline: none;
    color: #fff;
    background: #1a1a1a;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #34b093, #7a00ff, #34b093, rgb(97, 37, 223), #34b093);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.glow-on-hover:active {
    color: #000
}

.glow-on-hover:active:after {
    background: transparent;
}

.glow-on-hover:hover:before {
    opacity: 1;
}

.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #1a1a1a;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
        background-position: 0 0;
    }
}

@media only screen and (min-width: 320px) {
    .glow-on-hover {
        width: 100%;
        height: 70px;
    }
}

@media only screen and (min-width: 1024px) {
    .glow-on-hover {
        width: 350px;
        height: 70px;
    }
}